import React from 'react'
import './../styles/Landing.scss'
import illustration from './../../images/landing_illustration.svg'
import Translate from './../Translate'

function Landing() {
    return (
        <div className="Landing" id="Landing">
            <div className="illustration">
                <img src={illustration} alt='E-LEARNING' />
            </div>
            <div className="text">
                <p className="title">
                    <Translate string="landing.title" />
                </p>
                <p className="description">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                Morbi vitae volutpat erat. Praesent ut elit placerat, consectetur 
                nisl non, rutrum tortor. In hac habitasse platea dictumst.
                </p>
                <div className="buttons">
                    <button className="secondary">
                        <Translate string="landing.signup" />
                    </button>
                    <button className="primary" onClick={() => window.location.replace('#courses')}>
                        <Translate string="landing.courses" />
                    </button>
                </div>
            </div>
        </div>
    )
}

export default Landing
