import React, {useState, useEffect} from 'react'
import $ from 'jquery'
import '../styles/Loading.scss'
import Translate from './../Translate'

function Loading(props) {
    const loadingTime = parseInt(props.time)
    const timeoutTime = loadingTime + 600
    const [loading, setLoading] = useState(true)
    const language = props.language
    const n = language==='en'?0:1
    const loadingFinished = ['Loading finished!', 'Chargement t√©rmin√©!']
    useEffect(() => {
        setTimeout(() => {
            $('.Loading-text').text(loadingFinished[n])
        }, loadingTime)
        setTimeout(() => {
            setLoading(false)
            $('.Loading').addClass('hidden')
            setTimeout(() => {
                $('.Loading').remove()
            }, 400)
        }, timeoutTime)
    }, [])
    return (
        <div className="Loading">
            <p className="Loading-text">
                <Translate string='loading.loading' />
            </p>
            <div className="progress-bar">
                <span style={{animation: `loadUp ease-in-out forwards ${loadingTime}ms`}}></span>
            </div>
        </div>
    )
}

export default Loading
