import React, {useState, useEffect} from 'react'
import '../styles/Header.scss'
import {Link, BrowserRouter as Router} from 'react-router-dom'
import english from '../../images/en.jpg'
import french from '../../images/fr.jpg'
import $ from 'jquery'
import Translate from './../Translate'

function Header(props) {
    const changeLang = props.changeLang
    const toggleLanguage = props.toggleLanguage
    const languageMenuOpen = props.languageMenuOpen
    const currentLang = props.currentLang
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
    const menu = [
        {
            title: "nav.home",
            url: "#Landing"
        },
        {
            title: "nav.about",
            url: "#about"
        },
        {
            title: "nav.courses",
            url: "#courses"
        },
        {
            title: "nav.pricing",
            url: "#pricing"
        },
    ]
    const toggleMenu = () => {
        if (mobileMenuOpen) {
            setMobileMenuOpen(!mobileMenuOpen)
            setTimeout(() => {
                $('.mobile-menu').css('display', 'none')
            }, 420)
        } else {
            $('.mobile-menu').css('display', 'flex')
            setTimeout(() => {
                setMobileMenuOpen(!mobileMenuOpen)
            }, 20)
        }
    }
    return (
        <header className="flex scrolled">
            <div className="col1">
                <div className="Logo" onClick={()=>window.location.replace('/')}>
                    <p>
                        <Translate string="logo" />
                    </p>
                </div>
                    <nav className="hide-for-mobile">
                        <ul>
                            {menu.map(link => {
                                return (<li key={link.title}>
                                    <div>
                                        <a href={link.url}>
                                            <Translate string={link.title} />
                                        </a>
                                    </div>
                                </li>)
                            })}
                        </ul>
                    </nav>
            </div>
            <div className="col2">
                <div className={`mobile-menu hide-for-desktop${mobileMenuOpen?' open':''}`}>
                    <p>this is mobile menu</p>
                </div>
                <div className={`ham-menu hide-for-desktop${mobileMenuOpen?' open':''}`} onClick={toggleMenu}>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
                <div className="buttons hide-for-mobile">
                    <div className="button language drop-down">
                        <div className="current" onClick={toggleLanguage}>
                            <img src={currentLang==='en'?english:french} alt={currentLang==='en'?'english':'french'} />
                            <span className={languageMenuOpen?'flipped':''}></span>
                        </div>
                        <div className={`drop-down-content${languageMenuOpen?'':' hidden'}`}>
                            <div className="item english" onClick={()=>changeLang('en')}>
                                <img src={english} alt='english' />
                                <p>English</p>
                            </div>
                            <div className="item french" onClick={()=>changeLang('fr')}>
                                <img src={french} alt='french' />
                                <p>Fran√ßais</p>
                            </div>
                        </div>
                    </div>
                    <button className="button third">
                        <Translate string="header.signin" />
                    </button>
                    <button className="button primary">
                        <Translate string="header.signup" />
                    </button>
                </div>
            </div>
        </header>
    )
}

export default Header
