import React, {useState} from 'react'
import Header from './components/Header/Header'
import Loading from './components/Loading/Loading'
import Landing from './components/Landing/Landing'
import Translate from './components/Translate'
import './components/styles/main.scss'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import Cookies from 'js-cookie'
import { LocaleContext } from './locale-context';

function App() {
  var savedLang = Cookies.get('lang')
  const defaultLang = 'en'
  if (savedLang && !['en', 'fr'].includes(savedLang)) {
    Cookies.set('lang', 'en')
    savedLang = 'en'
  }
  const [currentLang, setCurrentLang] = useState(savedLang || defaultLang)
  const [languageMenuOpen, setLanguageMenuOpen] = useState(false)
  const changeLang = (to) => {
    setLanguageMenuOpen(false)
    if (['en', 'fr'].includes(to)) {
      setCurrentLang(to)
      Cookies.set('lang', to)
    }
  }
  const toggleLanguage = () => {
    setLanguageMenuOpen(!languageMenuOpen)
}
  return (
    <LocaleContext.Provider value={currentLang}>
      <Router>
        <Loading time='2000' language={currentLang} />
            <Switch>
              <Route exact path='/'>
                <Header currentLang={currentLang} languageMenuOpen={languageMenuOpen} changeLang={changeLang} toggleLanguage={toggleLanguage} />  
                <Landing />
                <div className="content" id='about'>
                  <h1><Translate string="about.title" /></h1>
                  <p>
                    Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32
                  </p>
                </div>    
                <div className="content" id='courses'>
                  <h1><Translate string="courses.title" /></h1>
                  <p>
                    Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32
                  </p>
                </div>
                <div className="content" id='pricing'>
                  <h1><Translate string="pricing.title" /></h1>
                  <p>
                    Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32
                  </p>
                </div>
              </Route>
              <Route path='/:inexistentPage'>
                <div className="content">
                  <h1>hoops, 404!</h1>
                </div>
              </Route>
            </Switch>
      </Router>
  </LocaleContext.Provider>
  );
}

export default App;
